export default {
	data() {
		return {
			list:[]
		};
	},
	onLoad() {
		this.getList()
	},
	methods: {
		getList(){
			this.$api.personalManual().then(res => {
				if(res.data.errno === 0){
					this.list = res.data.data
				}
			})
		},
		toUserManualDetail(item){
			uni.navigateTo({
				url: '/pages/mine/userManual/userManualDetail?key=' + item.key + '&title=' + item.title
			})
		}
	}
}
